@import "../../../css/module/fn";
@import "../../../css/module/color";

.mui-content {
	/*>div{
		padding: 0 !important;
	}*/
}

.mui-segmented-control {
	border: 2px solid $red;
	border-radius: 0;
	
	.mui-control-item {
		color: #f48d88;
		border-color: $red;
		background-color: $red;
		border-bottom: 2px solid $red;
		&.mui-active {
			color: #fff;
			background-color: $red;
			border-bottom: 2px solid white;
		}
	}
}

.mui-table-view {
	background-color: transparent;
	&:before {
		background-color: transparent;
	}
	&:after {
		background-color: transparent;
	}
	
	.mui-table-view-cell {
		margin: 20px 0;
		background-color: #fff;
		box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.3);
		border-radius: 4px;
		
		&:nth-of-type(1) {
			margin-top: 0;
		}
		&:after {
			background-color: transparent;
		}
		
		.mui-input-group:after{
			background-color: transparent;
		}
		
		h3{
			height: p2m(70);
			line-height: p2m(70);
			margin-bottom: 0;
			color: $red;
			text: {
				indent: p2m(80);
			}
			font: {
				size: p2m(28);
				weight: 600;
			}
			background:{
				repeat: no-repeat;
				position: 12px center;
				size: p2m(32) p2m(32);
			}
			
			&.h3type {
				background-image: url("./ico32-bloom.png");
			}
			&.h3time {
				background-image: url("./ico32-clock.png");
			}
		}
		.txt{
			text-indent: p2m(80);
			margin-top: p2m(-20);
		}
		.acceptTxt{
			text-indent: p2m(80);
			margin-top: p2m(-20);
			height: 3em;
			line-height: 3em;
			color: $red;
		}
		.pMsg{
			margin: p2m(16) 1em 2em ;
			text: {
				indent: 2em;
			}
			background:{
				image: url("./ico32-book.png");
				repeat: no-repeat;
				position: left 3px ;
				size: p2m(32) p2m(32);
			}
		}
		
		
		> .mui-navigate-right {
			font: {
				weight: 100;
				size: p2m(28);
			}
			&.unread{
				font-weight: 900 ;
			}
			
			color: black;
			
			height: p2m(80);
			
			.padRed{
				margin-top: -3px;
			}
		}
		
		.mui-popup-button {
			color: #fff;
			background-color: $red;
			font-size: p2m(26);
			box-shadow: inset 0 3px 3px 0 rgba(0, 0, 0, 0.2);

			
			&:first-child {
				border-radius: 0 0 0 4px;
			}
			&:last-child {
				border-radius: 0 0 4px 0;
			}
			&:after {
				background-color: white;
				width: 2px;
			}
		}
	}
}